import Logger from '../services/logger.service'
import flatten from '../services/flatten.js'
import unflatten from '../services/unflatten.js'
import util from '../services/util.js'

export const autocomplete = {
    methods: {
        //Autocomplete
        loadAutoComplete(e){
            let attribute = e.target.id            
            let path = this.f.search.url
            let value =e.target.value==null?"":e.target.value
            let otherValues = ""
            let index = attribute.indexOf("[")>0?attribute.substring(attribute.indexOf("[")+1,attribute.indexOf("]")):"-1"

            Logger("[loadAutoComplete]: attribute - "+attribute+ " index - "+index)
            Logger("[loadAutoComplete]: this.f.search - "+this.f.search)
            Logger("[loadAutoComplete]: this.f.search.dependencies - "+this.f.search.dependencies)
            if (this.f.search!=null){
                let dependencies = this.f.search.dependencies
                for (let i= 0; i <dependencies.length; i++){
                    if (otherValues!=""){
                        otherValues += "__AND__"
                    }
                    let depAttribute = dependencies[i].field.attribute
                    if (index!="-1" && depAttribute.indexOf("[-1]")){
                        depAttribute = this.getAttributeTable(index,depAttribute)
                    }
                    otherValues += depAttribute+"__=__"+eval(`this.item.${depAttribute}`)
                }
                if (otherValues!=''){
                    otherValues += "__AND__"
                    let position =attribute.indexOf(".")
                    let newAttrib = attribute 
                    if (position != -1){
                        newAttrib =  attribute.substring(position+1)
                    }
                    otherValues += newAttrib+"__:__"
                } 
                
                
            }
            this.cleanDatalist(attribute)
            
            Logger("[loadAutoComplete]: otherValues - " +otherValues)
            if (value!= ''){
                this.$store.dispatch('form/loadAutoComplete', {"path":path, "value":otherValues+value}).then(
                    autocomplete => {
                        if (autocomplete.data.status){
                            this.populateDatalist(autocomplete.data, attribute)
                        } else {
                            this.message = autocomplete.data.userMessageError+"."+autocomplete.data.technicalMessageError
                        }
                        this.loading = false;
                    },
                    error => {
                        this.message = error
                    }
                );
            }
            
                
        },
        populateDatalist(result, attribute){
            let data = result.data
            var base = ""
            var position = attribute.lastIndexOf(".");
            if (position > -1){ //is it an object?
                base = attribute.substring(0,position+1); //adding the prefix when it is an object
                data = this.FORM_AUTOCOMPLETE_FIXFIELDBASE(base,data,data.length);
            }

            let listOfAttribute = []
            let extraAttributes  = []
            for (var loop = 0; loop < data.length; loop++) {
                let extra = []
                Object.keys(data[loop]).forEach(function (item) {
                    var newItem = base+item;
                    if (attribute != newItem){
                        extra.push([newItem, eval("data["+loop+"]."+newItem)]);
                    }
                });
                listOfAttribute.push(eval("data["+loop+"]."+attribute))
                extraAttributes.push(extra)
            }

            position = -1
            for (var i=0; i<this.datalists.length;i++){
                if (this.datalists[i][0]==attribute){
                    position = i
                    break
                }
            }
            
            if (position != -1){
                this.datalists[position][1].splice(0, this.datalists[position][1].length)
                for (var x = 0 ; x < listOfAttribute.length;x++){
                    this.datalists[position][1].push(data[x])
                }
                this.datalists[position][2] = extraAttributes
            } else {
                this.datalists.push([attribute, listOfAttribute, extraAttributes])
            }
        },
        cleanDatalist(attribute){
            let position = -1
            for (var i=0; i<this.datalists.length;i++){
                if (this.datalists[i][0]==attribute){
                    position = i
                    break
                }
            }

            if (position != -1){
                this.datalists[position][1].splice(0, this.datalists[position][1].length)
            }
        },
        FORM_AUTOCOMPLETE_FIXFIELDBASE(base,data, size){
            var newData = JSON.stringify(flatten(data));
            var oldValue;
            var newValue;
            for(var i = 0 ; i < size ; i ++){
                oldValue = "["+i+"].";
                newValue = oldValue+base;
                newData = util.replaceAll(newData,oldValue,newValue);
            }	
            newData = unflatten(JSON.parse(newData));
            return newData;
        },
        FORM_AUTOCOMPLETE_DATALIST_ONCHANGE(e,selectedOption){
            Logger("FORM_AUTOCOMPLETE_DATALIST_ONCHANGE: "+ selectedOption)
            var attribute = e.target.id
            var input = document.getElementById(attribute);
            let index = attribute.indexOf("[")>0?attribute.substring(attribute.indexOf("[")+1,attribute.indexOf("]")):"-1"
            
            Logger("[loadAutoComplete]: attribute - "+attribute+ "index - "+index)
            Logger("[loadAutoComplete]: this.f.search - "+this.f.search)
            Logger("[loadAutoComplete]: this.f.search.returns - "+this.f.search.returns)

            if (this.f.search!=null){
                let returns = this.f.search.returns
                if (input.value != "" && selectedOption!=null){
                    for (let i= 0; i <returns.length; i++){
                        let toAttribute = returns[i].to.attribute
                        if (index!="-1" && toAttribute.indexOf("[-1]")){
                            toAttribute = this.getAttributeTable(index,toAttribute)
                        }
                        this.onInputAutoComplete(toAttribute, eval(`selectedOption.${returns[i].from.attribute}`))
                    }
                } else {
                    for (let i= 0; i <returns.length; i++){
                        let toAttribute = returns[i].to.attribute
                        if (index!="-1" && toAttribute.indexOf("[-1]")){
                            toAttribute = this.getAttributeTable(index,toAttribute)
                        }
                        this.onInputAutoComplete(toAttribute, '')
                    }
                }
                
            }
            
            /*
            var done = false;
            var pendentUpdate = []
        
            if (input.value != ""){
                if (selectedOption != null){
                    let position = attribute.lastIndexOf(".")
                    if (position > -1){
                        var items = []
                        var newFieldId = null
                        var newField = null
                        var newFieldValue = null
        
                        if (attribute.lastIndexOf(".",position-1)> -1){
                            Object.keys(selectedOption).forEach(function (item){
                                if(item != attribute.substring(position+1)){
                                    newFieldId =attribute.substring(0,position+1)+item
                                    newField = document.getElementById(newFieldId); 
                                    if (newField != null){
                                        newFieldValue = eval(`selectedOption.${attribute.substring(0,position+1)}${item}`)
                                        newField.value = newFieldValue
                                        pendentUpdate.push([newFieldId,newFieldValue])
                                        
                                        done = true;
                                    }
                                }
                            });
                        } else {
                            items =  Object.entries(eval(`selectedOption.${attribute.substring(0,position)}`)).map(([key, value])  => [key, value])
                            for (var k = 0; k < items.length; k++){
                                newFieldId = attribute.substring(0,position)+"."+items[k][0]
                                if (newFieldId != attribute){
                                    newField = document.getElementById(newFieldId); 
                                    if (newField != null){
                                        newFieldValue = items[k][1]
                                        newField.value = newFieldValue
                                        pendentUpdate.push([newFieldId,newFieldValue])

                                        done = true
                                    }
                                }
                            }
                        }
                        
                    }
                }
            }
            for (var i = 0; i < pendentUpdate.length;i++){
                this.onInputAutoComplete(pendentUpdate[i][0], pendentUpdate[i][1])
            }
            pendentUpdate = null
            if (!done){
                //winput.value = "";	
            }*/
        },
        getSeletedOption(datalist,attribute,value){
            Logger("getSeletedOption: "+ attribute)
            let sel = null
            for (let o = 0; o < datalist.length; o++ ){
                let option =  datalist[o]
                
                if (eval(`option.${attribute}`)==value){
                    if (attribute.indexOf("[")!=-1){
                        sel = eval(`option.${attribute.substring(0,attribute.indexOf("]")+1)}`)
                    } else {
                        sel = option
                    }
                    break
                }
            }
            return sel
        },
        getAutoCompleteHeader(data,attribute){
            Logger("getAutoCompleteHeader: "+ attribute)
            return eval(`data.item.${attribute}`)
        },
        getAutoCompleteBody(data,attribute){
            Logger("getAutoCompleteBody: "+ attribute)
            let body = ""
            let position = attribute.lastIndexOf(".")
            if (position > -1){
                let obj  = eval(`data.item.${attribute.substring(0,position)}`)
                Object.keys(obj).forEach(function (key) {
                    if(key != attribute.substring(position+1)){
                        let value = eval(`data.item.${attribute.substring(0,position+1)}${key}`)
                        if (body!=""){
                            body += " "
                        }
                        body += (value!=null?value:"") 
                    }
                })
            }
            return body
        },
        onChangeAutoComplete(e){
            Logger("onChangeAutoComplete: "+ e)
            this.FORM_AUTOCOMPLETE_DATALIST_ONCHANGE(e,
                    this.getSeletedOption(
                        this.getDatalist(e.target.id),
                        e.target.id,
                        document.getElementById(e.target.id).value))
        },
        onInputAutoComplete(key,value){
            Logger("onInputAutoComplete: "+ key)
            let aux = this.item
            let auxKey = key
            let auxValue = value
            let position = -1
            if ((position = auxKey.indexOf("]")) > -1){
                aux = eval(`this.item.${auxKey.substring(0,position+1)}`)
                auxKey = auxKey.substring(position+2)
            }
            while( (position = auxKey.indexOf("."))>0){
                aux = aux[auxKey.substring(0,position)]
                auxKey = auxKey.substring(position+1)
            }
            this.$set(aux,auxKey, auxValue)
        },
        //End auto complete
    }
}