<template>
    <v-card v-if="settings && item" :class="dialog?'':'mt-5 ml-3 mr-3'">
        <v-toolbar flat v-if="modal" :color="!this.$vuetify.theme.dark?'grey lighten-3':''">
            <v-btn icon text color="grey" @click="close">
                <v-icon v-if="!this.$vuetify.breakpoint.smAndDown">mdi-close</v-icon>
            </v-btn>
            <v-toolbar-title class="text grey--text">
                 <Logo :branchName="this.currentUser.branch.name" :environment="this.currentUser.module.description" />
            </v-toolbar-title>
            <v-spacer></v-spacer>
            <span :class="(!this.$vuetify.theme.dark?'black--text':'white--text')+' font-weight-light text-center mr-12 ml-n12 pr-12'" style="font-size:20px">{{settings.title}}</span>
            <v-spacer></v-spacer>
            <v-toolbar-items>
            <v-btn text color="grey" @click="close">
                <v-icon>mdi-close</v-icon>
                Close
            </v-btn>
            <!--
            <v-btn text color="grey" :loading="loading" :disabled="loading||message!=null" @click="submit">
                Submit
                <template v-slot:loader>
                    <span>Loading...</span>
                </template>
            </v-btn>
            -->
            </v-toolbar-items>
            <v-menu bottom right offset-y>
                <template v-slot:activator="{ on, attrs }">
                    <v-btn text color="grey" icon v-bind="attrs" v-on="on" >
                        <v-icon>mdi-dots-vertical</v-icon>
                    </v-btn>
                </template>
                <v-list > 
                    <v-list-item v-for="(b,index) in this.buttons" :key="index" color="blue darken-1" text 
                        @click="getFunction(b[0])">
                         <v-list-item-icon>
                            <v-icon v-text="b[2]"></v-icon>
                        </v-list-item-icon>
                        <v-list-item-content>
                            <v-list-item-title>{{b[0] }}</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                </v-list>
            </v-menu>
        </v-toolbar>
        <v-card-title v-if="!modal">
            {{settings.title}}
        </v-card-title>
        <v-card-text>
            <v-container>
                <v-form ref="form">
                    <v-alert v-if="message" type="error">
                        {{message}}
                        <v-btn text small @click="message = null">
                            <v-icon>mdi-close</v-icon>
                            Close
                        </v-btn>
                    </v-alert> 
                    <v-row>
                        <v-col v-for="f in this.settings.tabs[0].fields" :key="f.id"
                               :cols="getSizeByType('xs',f.width,f.field.type)" 
                               :sm="getSizeByType('sm',f.width,f.field.type)" 
                               :md="getSizeByType('md',f.width,f.field.type)"
                               :lg="getSizeByType('lg',f.width,f.field.type)" 
                               :xl="getSizeByType('xl',f.width,f.field.type)" >
                            <Field :f=f /> 
                        </v-col>
                    </v-row>
                    <v-row v-for="t in this.tables" :key="t[0]">
                        <v-col cols="12">
                            <v-btn color="primary" depressed small @click="addLine(t[0])"> 
                                <v-icon small>mdi-folder-plus</v-icon>
                            </v-btn>
                        </v-col>
                        <v-col cols="12">
                            <v-simple-table dense>
                                <template v-slot:default>
                                    <thead>
                                        <tr>
                                            <th class="text-left" v-for="c in t[1]" :key="c.id">
                                                {{c.field.title}}
                                            </th>
                                            <th>
                                                #
                                            </th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(f,index) in t[3]" :key="index">
                                            <td v-for="f in t[1]" :key="f.id" class="form-table-column">
                                                <Column :f=f :index=index />
                                            </td>
                                            <td v-if="t[3].length > 0">
                                                <v-btn color="error" depressed small @click="removeLine(t[0],index)">
                                                    <v-icon small>mdi-delete</v-icon>
                                                </v-btn>
                                            </td>
                                        </tr>
                                    </tbody>
                                </template>
                            </v-simple-table>
                        </v-col>
                    </v-row>
                </v-form>
            </v-container>
        </v-card-text>
        <v-card-actions v-if="modal">
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="close" >TOTAL: </v-btn>
        </v-card-actions>
        <v-card-actions v-if="!modal">
            <v-spacer></v-spacer>
            
            <v-btn color="blue darken-1" text @click="close" >Close</v-btn>
            <!--
            <v-btn color="blue darken-1" :loading="loading" :disabled="loading||message!=null" text @click="submit">
                Submit
                <template v-slot:loader>
                    <span>Loading...</span>
                </template>
            </v-btn>
            -->
            <v-btn v-for="(b,index) in this.buttons" :key="index" color="blue darken-1" text @click="getFunction(b[0])" >
                {{b[0] }} 
            </v-btn>
        </v-card-actions>
    </v-card>
</template>

<script>
import Logo from './Logo'
import Column from './Column'
import Field from './Field'
import Logger from '../services/logger.service'
//import myfunction from '../services/myfunction.js'
import unflatten from '../services/unflatten.js'
import parsefunction from '../services/parsefunction.js'

import {formcomputedattributes } from '../mixins/formcomputedattributes'
import {parsejson } from '../mixins/parsejson'
import {getsize } from '../mixins/getsize'
import {loggedUser } from '../mixins/loggedUser'

export default {
    name: 'Form',
    components: {
        Logo,Field, Column
    },
    props: {
      settings: Object,
      myItem : Object,
      dialog : Boolean,
      modal : Boolean
    },
    mixins : [formcomputedattributes,getsize,parsejson,loggedUser],
    data (){
        return ({
            loading : false,
            message : null,
            tables : [],
            watchers : [],  
            hasFile : false,
        })
    },
    methods: {
        generateButtons(obj){  
            if (obj!=null){
                this.buttons = []
                for (let i =0; i < obj.length; i++){
                  this.buttons.push([obj[i].name, parsefunction(obj[i].jsFunction),obj[i].icon])
                }
            }
            
        },
        addLine(tableId){
            Logger("addLine: "+ tableId)
            let position = -1
            for (let t = 0; t < this.tables.length;t++){
                if (tableId==this.tables[t][0]){
                    position = t
                    break
                }
            }
            if (position > -1){
                let newLine = this.tables[position][4]
                let newLineObj = JSON.parse(newLine)
                let newUnflattenLineObj = unflatten(newLineObj)
                this.tables[position][3].push(newUnflattenLineObj)
            }
        },
        removeLine(tableId,index){
           Logger("removeLine: "+ tableId+","+index)
           let position = -1
            for (let t = 0; t < this.tables.length;t++){
                if (tableId==this.tables[t][0]){
                    position = t
                    break
                }
            }
            if (position > -1){
                this.tables[position][3].splice(index, 1)
            }
        }, 
        getFunction(name){
            Logger("getFunction:"+name)
            return this.executeFunc(name)
        }, 
        executeFunc(name) {
            Logger("executeFunc:" + name)
            let position = -1
            for (let i = 0; i < this.buttons.length; i ++){
                if (this.buttons[i][0]==name){
                    position = i;
                    break;
                }
            }
            if (position != -1) {
                let _this = this
                this.buttons[position][1](_this)
            } else {
                console.warn(`${name} was not yet defined!`)
            }
        },     
        generateObj(){
            Logger("generateObj")
            let myObj ="{"
            let tables = []
            let fields = this.settings.tabs[0].fields
            for (var i = 0; i < fields.length;i++){
                //Check table
                let position = fields[i].field.attribute.indexOf("[-1]");
                if (position > 0){
                    let inputID = fields[i].field.attribute.substring(0,position+4 );
                    inputID = inputID.substring(0,fields[i].field.attribute.indexOf("[-1]"))
                    position = -1;
                    for ( let t = 0; t < tables.length; t ++){
                        if (tables[t][0] == inputID ){
                            position = t;
                            break;
                        }
                    }
                    if (position == -1){
                        if (i>0){
                            myObj += "," 
                        }            
                        myObj += '"'+inputID+'" : [] '
                        /*  	    0 - id      1 - fields, 2-data,     3- reference to item, 4-json of a new row */
                        tables.push( [ inputID, [fields[i] ], []        , []                    , "" ] );
                    } else {
                        tables[position][1].push( fields[i] );
                    }
                    continue;
                }

                position = fields[i].field.attribute.indexOf("[");
                if (position > 0 && fields[i].field.attribute.substring(position+1,1) != "-"){
                    let inputID = fields[i].field.attribute.substring(0,position)
                    position = -1;
                    for ( let t = 0; t < tables.length; t ++){
                        if (tables[t][0] == inputID+"[-1]" ){
                            position = t;
                            break;
                        }
                    }
                    tables[position][2].push( fields[i] );
                    continue;
                }
                if (this.myItem==null){
                    myObj = this.attributeToJson(myObj,i,fields)
                }
            }
            myObj += "}"
            Logger("generateObj: " +myObj)
            this.files = []
            if (this.myItem==null){
                this.item = unflatten(JSON.parse(myObj))
            }else {
                this.item = JSON.parse(JSON.stringify(this.parseJSONObject(this.myItem)))
            }
            this.tables = tables

            for (let t = 0; t < this.tables.length; t++){
                tables[t][3] = eval(`this.item.${tables[t][0]}`)//ref of obj
                myObj = "{"
                let myCustomTable = tables[t][1]
                for (let i = 0; i < myCustomTable.length; i++){
                    myObj = this.attributeToJson(myObj,i,myCustomTable)
                }
                myObj += "}"
                tables[t][4] = myObj 
            }
            this.generateButtons(this.settings.buttons)
        },
        attributeToJson(myObj,i,fields){
            if (i>0){
                myObj += "," 
            }         
            if(fields[i] == null || fields[i].field == null) {
                console.log("attributeToJson - NULL! CONTACT ADMINISTRATOR: " +JSON.stringify(fields[i]))
            }
             
            let type = fields[i].field.type.toLowerCase()
            let value = fields[i].field.value

            if (type.includes('select')){
                if (value==null){
                    console.log(fields[i].field.attribute + " - invalid attribute - " + type)
                }
                let auxValue = this.parseJSONAttribute(value)
                if (type.includes('selectboolean')){
                    type = 'boolean'
                } else if (type.includes('selecttext')) {
                    type = 'text'
                } else {
                    type = 'number'
                }
                for (let i=0; i < auxValue.length; i++){
                    if (auxValue[i].selected){
                        value = auxValue[i].value
                        break
                    }
                }
            }

            let attribute = fields[i].field.attribute
            let position = attribute.indexOf("[-1]")
            if (position>0){
                attribute =  attribute.substring(position+5)
            }

            if (type.includes('date') || 
                type.includes('email') ||
                type.includes('password') ||
                type.includes('text') ||
                type.includes('textarea') ){
                myObj += '"'+attribute + '":"' + this.parseJSONAttribute((value!=null?value:""))+ '"' 
            } else { //Checkbox, Number, Boolean
                myObj += '"'+attribute + '":' + this.parseJSONAttribute(value)
            }
            return myObj
        },
        submit(){
            Logger("submit")
            if (!confirm('Are you sure?')){
                return 
            }

            this.loading = true;
            if(this.$refs.form.validate()) {
                this.$store.dispatch('form/submit', {"path": this.settings.url, "data":this.item, "files": this.files}).then(
                    form => {
                        if (form.data.status){
                            this.close()
                        } else {
                            this.message = form.data.userMessageError+"."+form.data.technicalMessageError
                        }
                        this.loading = false;

                    },
                    error => {
                        this.message = error
                    }
                );
                setTimeout(() => { 
                    this.loading = false
                }, 1000)
            } else {
                this.loading = false;
            }
            
        },
        close () {
           this.clean()
           this.loading = false
           this.$emit("dialog-close")
        },
        clean(){
           this.rules = []
           this.item = null
           this.datalists = []
           this.message = null
        }
    },
    watch: {
        settings:{
            immediate: true,
            deep: true,
            handler() {
                this.clean()
                this.generateObj()
            }
        },
        watchers:{
            immediate: true,
            deep: true,
            handler(newVal/*, oldVal*/) {
                //console.log(newVal)
                //console.log(oldVal)
                var self = this;
                newVal.forEach(function(item) {
                   self.$watch(item.attribute,item.function);
                })
            }
        }
    },
}



</script>

<style>
.form-table-column{
	background: transparent; 
	border: none; 
	outline: none; 
	width: 100%; 
	min-width:150px; 
	box-sizing: border-box; 
	-moz-box-sizing: border-box; 
	-webkit-box-sizing: border-box;	
}
</style>