<template>
    <v-row v-if="this.filters">
        <v-col col="12" offset-md="2" md="8">
            <v-btn small text color="grey"  @click="addFilter()">
                <v-icon>mdi-folder-plus</v-icon>
            </v-btn>
            {{this.message}}
        </v-col>
        <v-col col="12" offset-md="2" md="8">
            <v-simple-table fixed-header dense v-if="this.filters.length >0">
                <template v-slot:default>
                    <thead>
                        <tr>
                            <th class="text-left">Attribute</th>
                            <th class="text-left">Operation</th>
                            <th class="text-left">Value</th>
                            <th class="text-left">Condition</th>
                            <th class="text-left">User filter?</th>
                            <th class="text-center">#</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(filter,index) in filters" :key="index">
                            <td class="form-table-column">
                                <v-select
                                    v-model="filter.attribute"
                                    :items="fields"
                                    item-value="field.attribute"
                                    item-text="field.title"
                                    :readonly="!filter.isUserFilter"
                                    single-line
                                />
                            </td>
                            <td  width="70px">
                                <v-select
                                    v-model="filter.operation"
                                    :items="getOperation(getType(filter.attribute))"
                                    :readonly="!filter.attribute || !filter.isUserFilter"
                                    single-line
                                />
                            </td>
                            <td class="form-table-column">
                                <v-text-field 
                                    :type="getType(filter.attribute)"
                                    label="Value"
                                    v-model="filter.value"
                                    :readonly="!filter.isUserFilter"
                                    single-line
                                />
                            </td>
                            <td width="70px">
                                <v-select
                                    v-model="filter.condition"
                                    :items="conditions"
                                    :readonly="!filter.isUserFilter"
                                    single-line
                                />
                            </td>
                            <td width="50px">
                                <v-checkbox v-model="filter.isUserFilter"
                                    :disabled="true"
                                    :readonly="true"
                                />
                            </td>
                            <td>
                                <v-btn small text color="grey" @click="removeFilter(index)" :disabled="!filter.isUserFilter">
                                    <v-icon>mdi-delete</v-icon>
                                </v-btn>
                            </td>
                        </tr>
                    </tbody> 
                </template>
            </v-simple-table>
        </v-col>
        <v-col col="12" sm="12" offset-md="8" md="2">
            <v-btn small text color="grey" :loading="loading" @click.prevent="_this.loadAdvancedFilter()">
                <v-icon>mdi-magnify</v-icon>
                
            </v-btn>
        </v-col>
    </v-row>
</template>

<script>


export default {
    name: 'DynamicFilter',
    props: {
        fields : Array,
        filters : Array,
        _this : Object,
        loading : Boolean
    },
    data (){
        return ({
            conditions : ["AND","OR"],
            operations : ["=",">=",">","<","<=","!=","like"],
            message:null
        })
    },
    methods: {
        getOperation(type){
            let currentOp = []

            if (type == "text"){
                currentOp = this.operations
            } else {
                for (let i = 0; i < this.operations.length-1;i++){
                    currentOp.push(this.operations[i])
                }
            }
            return currentOp
        },
        getType(attribute){
            let type = "TEXT";
            if (attribute!=null){
                for(let i = 0; i < this.fields.length;i++ ){
                    if (this.fields[i].field.attribute == attribute){
                        type = this.fields[i].field.type
                        break;
                    }
                }
            }
           return type.toLowerCase()
        },
        checkFilter(index){
            if (this.filters.length>0){
            
                if (this.filters.length > 1 && this.filters[index].condition==null){
                    this.message = "Condition is invalid"
                    return false
                }
                
                if (this.filters[index].attribute == null){
                    this.message = "Attribute is invalid"
                    return false
                }
                if (this.filters[index].operation == null){
                    this.message = "Operation is invalid"
                    return false
                }
                if (this.filters[index].value == null){
                    this.message = "Value is invalid"
                    return false
                }
            }
            return true
        },
        addFilter(){
            if(this.checkFilter(this.filters.length-1)){
                this.filters.push({
                    "condition" : null,
                    "attribute" : null,
                    "operation" : null,
                    "value"     : null,
                    "isUserFilter" : true
                })
            }            
        },
        removeFilter(index){
            if (this.filters[index].isUserFilter){
                this.filters.splice(index,this.filters.length-index)    
            }
        }
    }
}
</script>


<style>
.form-table-column{
	background: transparent; 
	border: none; 
	outline: none; 
	width: 100%; 
	min-width:150px; 
	box-sizing: border-box; 
	-moz-box-sizing: border-box; 
	-webkit-box-sizing: border-box;	
}
</style>