<template>
    <v-app>
        <Navbar></Navbar>
        <v-content>
            <v-container class="px-2 py-0 fill-height" fluid>
                <v-alert v-if="message" type="error">
                    {{message}}
                </v-alert> 
                <v-row v-for="r in row" :key="r" class="this.$route.params.workspaceId>0?'fill-height':''" >
                    <v-col v-for="c in col" :key="c" 
                        :cols="getColumnSize(col)" 
                        :sm="getColumnSize(col)" 
                        :md="getColumnSize(col)"
                        :lg="getColumnSize(col)" 
                        :xl="getColumnSize(col)">
                        <div v-for="e in elements" :key="e.id">
                            <Datatable v-if="e.type.toUpperCase()=='TABLE'"     :settings="parseJSONObject(e)" ></Datatable>
                            <Form      v-else-if="e.type.toUpperCase()=='FORM'" :settings="parseJSONObject(e)" ></Form>
                        </div>
                    </v-col>
                </v-row>   
                
                <v-row v-if="this.$route.params.workspaceId==0" class="fill-height">
                    <Dashboard></Dashboard>
                </v-row>
            </v-container>     
        </v-content>
        <v-footer class="py-3">
            <span class="ml-auto overline">Caique Bispo Ferreira &copy;2020</span>
        </v-footer>
    </v-app>
</template>

<script>
import Dashboard from '../components/Dashboard'
import Navbar from '../components/Navbar';
import Form from '../components/Form'
import Datatable from '../components/Datatable'
import {parsejson } from '../mixins/parsejson'
import {getsize } from '../mixins/getsize'
import {loggedUser } from '../mixins/loggedUser'

export default{
    name : "Workspace",
    components : {
        Navbar,Form, Datatable,Dashboard
    },
    mixins : [parsejson,getsize,loggedUser],
    data(){
        return {
            row : 1,
            col : 1,
            elements : [],
            message : null
        }
    },
    mounted() {
        this.open()
    },
    methods:{
        getColumnSize(col){
            let size = 12/col
            return this.getSize("12 12 12 12 "+size)
        },
        open (){
            if (this.$route.params.workspaceId == 0){
                return 
            }
            this.$store.dispatch('workspace/open', {"languageId": this.currentUser.language.id,
                                                    "workspaceId": this.$route.params.workspaceId}).then(
                workspace => {
                    if (workspace.data.status){
                        this.row = workspace.data.data.row
                        this.col = workspace.data.data.column
                        this.elements = workspace.data.data.elements
                    } else {
                        console.log(workspace.data)
                        this.message = workspace.data.userMessageError+"."+workspace.data.technicalMessageError
                    }
                },
                error => {
                    console.log(error)
                }
            );
        }
    },
    watch: {
        // call again the method if the route changes
        '$route': 'open'
    },
}
</script>

