export const getsize = {
    methods: {
        getSizeByType(device,col,type){
            let size = this.getSize(device,col)
            
            if (type=='HIDDEN'){
                size = '0'
            }
            return size
        },
        getSize(device,col){
            var size = ""
            let array = col!=null?col.split(" "):[]
            if (array.length > 0){
                if (device == 'xs'){
                    if (array.length >= 1 ){
                        size = array[0]
                    }
                } else if (device == 'sm'){
                    if (array.length >= 2 ){
                        size = array[1]
                    } else {
                        size = array[array.length-1]
                    }
                } else if (device == 'md'){
                    if (array.length >= 3 ){
                        size = array[2]
                    } else {
                        size = array[array.length-1]
                    }
                } else if (device == 'lg'){
                    if (array.length >= 4 ){
                        size = array[3]
                    } else {
                        size = array[array.length-1]
                    }
                } else if (device == 'xl'){
                    if (array.length >= 5 ){
                        size = array[4]
                    } else {
                        size = array[array.length-1]
                    }
                }
            } else {
                size = '12'
            }
            return size
        },
    }
}