export const formcomputedattributes = {
    computed: {
        datalists: {
            get: function () {
                return this.$store.state.form.datalists;
            },
            set: function (newValue) {
                this.$store.state.form.datalists =  newValue
            }
        },
        files : {
            get: function () {
                return this.$store.state.form.files;
            },
            set: function (newValue) {
                this.$store.state.form.files = newValue
            }
        },
        item: {
            get: function () {
                return this.$store.state.form.item;
            },
            set: function (newValue) {
                this.$store.state.form.item =  newValue
            }
        },
        rules: {
            get: function () {
                return this.$store.state.form.rules;
            },
            set: function (newValue) {
                this.$store.state.form.rules =  newValue
            }
        },
        buttons : {
            get: function () {
                return this.$store.state.form.buttons;
            },
            set: function (newValue) {
                this.$store.state.form.buttons = newValue
            }
        },
    }, 
}