<template>
    <input v-if="f.field.type == 'hidden' " type="hidden"  >
    <!--:_url ="btoa(f.url)"-->
    <v-autocomplete v-else-if="f.search != null" :id="getAttributeTable(index,f.field.attribute)"
        :_url ="f.url"
        :disabled="f.readonly"
        :messages="f.field.observation"
        :name="getAttributeTable(index,f.field.attribute)"
        :placeholder="f.field.help"
        :readonly="f.readonly"
        :required="f.mandatory"            
        :rules="getRules(f.field.attribute,f.mandatory,f.field.type)"
        :value="onValueTable(f.field.type,index,f.field.attribute)"  
        @input="onInputTable(index,f.field.attribute,$event)" 
        :items="getDatalistForTable(index,f.field.attribute)"  
        @input.native="loadAutoComplete"
        @change.native="onChangeAutoComplete" 
        :item-text="getAttributeTable(index,f.field.attribute)"
        :item-value="getAttributeTable(index,f.field.attribute)"
        :no-filter=true 
        return-object 
        > 
        
        <template slot="item" slot-scope="data" >     
            <template v-if="typeof data.item !== 'object'" >   
                <v-list-item-content v-text="data.item"></v-list-item-content>   
            </template>
            <template v-else>        
                <v-list-item-content> 
                    <v-list-item-title class="v-list-item__title">{{getAutoCompleteHeaderForTable(index,data,f.field.attribute)}}</v-list-item-title>  
                    <v-list-item-subtitle class="v-list-item__subtitle">{{getAutoCompleteBodyForTable(index,data,f.field.attribute)}}</v-list-item-subtitle>    
                </v-list-item-content>
            </template>
        </template>
    </v-autocomplete> 
    <v-checkbox v-else-if="f.field.type == 'CHECKBOX'" :id="getAttributeTable(index,f.field.attribute)"
                :disabled="f.readonly"
                :messages="f.field.observation"
                :name="getAttributeTable(index,f.field.attribute)"
                :placeholder="f.field.help"
                :readonly="f.readonly"
                :required="f.mandatory"            
                :rules="getRules(f.field.attribute,f.mandatory,f.field.type)"
                :input-value="onValueTable(f.field.type,index,f.field.attribute)" 
                @change="onInputTable(index,f.field.attribute,$event)">
    </v-checkbox>
    <v-text-field v-else-if="f.field.type == 'DATE' " :id="getAttributeTable(index,f.field.attribute)"
                type="date" :disabled="f.readonly"
                :messages="f.field.observation"
                :name="getAttributeTable(index,f.field.attribute)"
                :placeholder="f.field.help"
                :readonly="f.readonly"
                :required="f.mandatory"            
                :rules="getRules(f.field.attribute,f.mandatory,f.field.type)"
                :value="onValueTable(f.field.type,index,f.field.attribute)" 
                @input="onInputTable(index,f.field.attribute,$event)"  >
    </v-text-field>
    <v-text-field v-else-if="f.field.type == 'DATETIME' " :id="getAttributeTable(index,f.field.attribute)"
                type="datetime-local" :disabled="f.readonly"
                :messages="f.field.observation"
                :name="getAttributeTable(index,f.field.attribute)"
                :placeholder="f.field.help"
                :readonly="f.readonly"
                :required="f.mandatory"            
                :rules="getRules(f.field.attribute,f.mandatory,f.field.type)"
                :value="onValueTable(f.field.type,index,f.field.attribute)"  
                @input="onInputTable(index,f.field.attribute,$event)"  >
    </v-text-field>
    <v-text-field v-else-if="f.field.type == 'EMAIL' " :id="getAttributeTable(index,f.field.attribute)"
                type="email" :disabled="f.readonly"
                :messages="f.field.observation"
                :name="getAttributeTable(index,f.field.attribute)"
                :placeholder="f.field.help"
                :readonly="f.readonly"
                :required="f.mandatory"            
                :rules="getRules(f.field.attribute,f.mandatory,f.field.type)"
                :value="onValueTable(f.field.type,index,f.field.attribute)"  
                @input="onInputTable(index,f.field.attribute,$event)"  >
    </v-text-field>
    <v-text-field v-else-if="f.field.type == 'NUMBER' " :id="getAttributeTable(index,f.field.attribute)"
                type="number" :disabled="f.readonly"
                :messages="f.field.observation"
                :name="getAttributeTable(index,f.field.attribute)"
                :placeholder="f.field.help"
                :readonly="f.readonly"
                :required="f.mandatory"            
                :rules="getRules(f.field.attribute,f.mandatory,f.field.type)"
                :value="onValueTable(f.field.type,index,f.field.attribute)"  
                @input="onInputTable(index,f.field.attribute,$event)"  >
    </v-text-field>
    <v-text-field v-else-if="f.field.type == 'PASSWORD' " :id="getAttributeTable(index,f.field.attribute)"
                type="password" :disabled="f.readonly"
                :messages="f.field.observation"
                :name="getAttributeTable(index,f.field.attribute)"
                :placeholder="f.field.help"
                :readonly="f.readonly"
                :required="f.mandatory"            
                :rules="getRules(f.field.attribute,f.mandatory,f.field.type)"
                :value="onValueTable(f.field.type,index,f.field.attribute)"  
                @input="onInputTable(index,f.field.attribute,$event)"  >
    </v-text-field>
    <v-select v-else-if="f.field.type.includes('SELECT')" :id="getAttributeTable(index,f.field.attribute)"
                :disabled="f.readonly"
                :messages="f.field.observation"
                :name="getAttributeTable(index,f.field.attribute)"
                :placeholder="f.field.help"
                :readonly="f.readonly"
                :required="f.mandatory"  
                :rules="getRules(f.field.attribute,f.mandatory,f.field.type)"
                :value="onValueTable(f.field.type,index,f.field.attribute)" 
                @input="onInputTable(index,f.field.attribute,$event)"
                :items="parseJSONAttribute(f.field.value)"
                item-value="value"
                item-text="description"
    ></v-select>
    <v-text-field v-else-if="f.field.type == 'TEXT' " :id="getAttributeTable(index,f.field.attribute)"
                type="text" :disabled="f.readonly"
                :messages="f.field.observation"
                :name="getAttributeTable(index,f.field.attribute)"
                :placeholder="f.field.help"
                :readonly="f.readonly"
                :required="f.mandatory"            
                :rules="getRules(f.field.attribute,f.mandatory,f.field.type)"
                :value="onValueTable(f.field.type,index,f.field.attribute)"  
                @input="onInputTable(index,f.field.attribute,$event)"  >
    </v-text-field>
    <v-textarea v-else-if="f.field.type == 'TEXTAREA' " :id="getAttributeTable(index,f.field.attribute)"
                type="textarea" :disabled="f.readonly"
                :messages="f.field.observation"
                :name="getAttributeTable(index,f.field.attribute)"
                :placeholder="f.field.help"
                :readonly="f.readonly"
                :required="f.mandatory"   
                :rows="f.rows"         
                :rules="getRules(f.field.attribute,f.mandatory,f.field.type)"
                :value="onValueTable(f.field.type,index,f.field.attribute)"  
                @input="onInputTable(index,f.field.attribute,$event)"  >
    </v-textarea>
    <v-text-field v-else-if="f.field.type == 'TIME' " :id="getAttributeTable(index,f.field.attribute)"
                type="time" :disabled="f.readonly"
                :messages="f.field.observation"
                :name="getAttributeTable(index,f.field.attribute)"
                :placeholder="f.field.help"
                :readonly="f.readonly"
                :required="f.mandatory"            
                :rules="getRules(f.field.attribute,f.mandatory,f.field.type)"
                :value="onValueTable(f.field.type,index,f.field.attribute)"  
                @input="onInputTable(index,f.field.attribute,$event)"  >
    </v-text-field>
</template>


<script>
import {formcomputedattributes } from '../mixins/formcomputedattributes'
import {attribute } from '../mixins/attribute'
import {autocomplete } from '../mixins/autocomplete'
import Logger from '../services/logger.service'
import {parsejson } from '../mixins/parsejson'

export default {
    name: 'Field',
    props: {
        f : Object,
        index : Number
    },
    mixins : [formcomputedattributes,attribute,autocomplete,parsejson],
    data (){
        return ({

        })
    },
    methods: {
        getAttributeTable(index,key){
            Logger("getAttributeTable: attribute - "+ key + " / index - "+index )
            let attribute = index==null? key : key.replace("[-1]","["+index+"]")
            return attribute
        },
        getDatalistForTable(index,attribute){
            attribute = attribute.replace("[-1]","["+index+"]")
            return this.getDatalist(attribute)
        },
        onInputTable(index,key,value){
          key = key.replace("[-1]","["+index+"]")
          let position = key.indexOf("]") 
          Logger("onInputTable: "+ key)
          if (key.indexOf(".")>0){
            let aux = position==-1?this.item: eval(`this.item.${key.substring(0,position+1)}`)
            let auxKey = position==-1?key:key.substring(position+2)
            let auxValue = value
            position = -1
            while((position = auxKey.indexOf("."))>0){
                aux = aux[auxKey.substring(0,position)]
                auxValue = typeof auxValue=='undefined'?auxValue:auxValue[auxKey.substring(0,position)]
                auxKey = auxKey.substring(position+1)
            }
            auxValue =position!=-1?typeof auxValue=='undefined'?auxValue:eval(`auxValue.${auxKey}`):auxValue
            this.$set(aux,auxKey, auxValue)
          } else {
            this.$set(this.item,key, value);
          }
        },
        onValueTable(type,index, key){
           return this.onValue(type,key.replace("[-1]","["+index+"]"))
        },
        //Autocomplete column
        getAutoCompleteHeaderForTable(index,data,attribute){
            Logger("getAutoCompleteHeaderForTable: "+ attribute)
            return this.getAutoCompleteHeader(data,this.getAttributeTable(index,attribute))
        },
        getAutoCompleteBodyForTable(index,data,attribute){
            return this.getAutoCompleteBody(data,this.getAttributeTable(index,attribute))
        },
        //end
    }
}
</script>