<template>
  <v-card v-if="settings" class="mt-5 ml-3 mr-3">
    <v-card-title class="font-weight-light">
      {{settings.title}}
      <v-spacer></v-spacer>
      <!--
      <v-select v-model="enabled" :items="slots" label="Slot"></v-select>
      -->
    </v-card-title>
    <v-card-subtitle>
       <v-alert v-if="message" type="error">
        {{message}} 
        <v-btn text small @click="message = null">
          <v-icon>mdi-close</v-icon>
          Close
        </v-btn>
      </v-alert> 
    </v-card-subtitle>
    <v-container>
      <v-data-table
        class="pa-2"
        :headers="headers"
        :items="items"
        :loading=loading loading-text="Loading... Please wait"
        multi-sort
        :options.sync="options"
        :items-per-page="options.itemsPerPage"
        :page.sync="options.page"
        :sort-by.sync="options.sortBy"
        :sort-desc.sync="options.sortDesc" 
        :server-items-length="total"
        @page-count="pageCount = $event"
        :search="search"
        :show-select="showSelect"
        :item-key="this.key"
        :footer-props="footerProps"
        >
        <!-- fixed-header height="400px"  window.screen.height * window.devicePixelRatio -->
        <!-- avoid surrogate primary key -->
        <template v-if="isEnabled('select')" v-slot:item.data-table-select="{ isSelected, select }">
          <v-simple-checkbox color="green" :value="isSelected" @input="select($event)"></v-simple-checkbox>
        </template>

        <template v-slot:top >
          <v-toolbar flat :color="$vuetify.theme.dark?'#1E1E1E':'white'">
            <v-text-field v-model="search" class="pt-4" append-icon="mdi-magnify" label="Search" single-line  ></v-text-field>
            <v-divider class="mx-4" inset vertical></v-divider>
            <v-btn text color="grey" class="pt+8" v-on:click="showAdvancedFilter = !showAdvancedFilter">
              <v-icon v-if="!showAdvancedFilter">mdi-filter</v-icon> 
              <v-icon v-if="showAdvancedFilter">mdi-filter-outline</v-icon>
            </v-btn>
            <v-divider class="mx-4" inset vertical></v-divider>
            <v-menu offset-y>
              <template v-slot:activator="{ on }">
                  <v-btn text color="grey" v-on="on" >
                    <v-icon>menu</v-icon>
                  </v-btn>
              </template>
              <v-list>
                  <v-list-item v-for="action in others" :key="action.id" @click.prevent="execOtherActions(action.actionType,action.actionSettings)">
                    <v-list-item-icon>
                        <v-icon v-text="action.icon"></v-icon>
                    </v-list-item-icon>
                    <v-list-item-content>
                      <v-list-item-title>{{ action.actionDescription }}</v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
              </v-list>
            </v-menu>
          </v-toolbar>
          <DynamicFilter v-if="showAdvancedFilter" 
              :fields="settings.fields" 
              :filters="filters" :_this="getThisInst()"
              :loading="loading" ></DynamicFilter>

          <v-dialog v-model="dialog" persistent hide-overlay transition="dialog-bottom-transition" scrollable 
            :x-large="settingsForm != null && settingsForm.tabs[0].fields.length <= 9" 
            :fullscreen="settingsForm != null && settingsForm.tabs[0].fields.length > 9" >
            <Form v-if="settingsForm" :modal=true :settings="settingsForm" :myItem="myItem" :dialog="dialog" @dialog-close="close()"></Form>
          </v-dialog>
        </template>
        <template v-if="isEnabled('default')" v-slot:item.actions="{ item }">
          <v-menu offset-y>
            <template v-slot:activator="{ on }">
                <v-btn small text color="grey" v-on="on" >
                <v-icon>mdi-dots-vertical</v-icon>
                </v-btn>
            </template>
            <v-list>
                <v-list-item v-for="action in defaultRowActions" :key="action.id" @click="execAction(getKey(item),action.actionUrl,action.actionType,action.actionSettings)" >
                  <v-list-item-icon>
                      <v-icon v-text="action.icon"></v-icon>
                  </v-list-item-icon>
                  <v-list-item-content>
                    <v-list-item-title>{{ action.actionDescription }}</v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
            </v-list>
          </v-menu>
         
        </template>
      </v-data-table>
    </v-container>
  </v-card>
</template>
  
<script>
import DynamicFilter from './DynamicFilter'
import Form from './Form'
import Logger from '../services/logger.service'

export default {
    name: 'Datatable',
    components : {
      Form,DynamicFilter
    },
    props: {
      settings: Object
    },
    data (){
      let systemFilter = []
      let defaultHeader = this.getDefaultHeader()
      const defaultSort = this.getDefaultSort()
      const slots = [
          'default',
          'select'
        ]
      
      if (typeof(this.$route.params.workspaceFilter) !== 'undefined') {
        //console.log('workspacefilter' + this.$route.params.workspaceFilter)
        //let workspaceFilterB64  = window.btoa('[{ "attribute": "id", "operation": "=", "value":1, "condition": null, "isUserFilter" : false }]')
        let workspaceFilterB64 = this.$route.params.workspaceFilter
        let workspaceFilter = JSON.parse(window.atob(workspaceFilterB64))
        for (let i = 0; i < workspaceFilter.length; i++ ){
          systemFilter.push(workspaceFilter[i])
        }
      }

      return ({
        defaultHeader :defaultHeader,
        defaultRowActions : JSON.parse(JSON.stringify(defaultHeader[1])),
        dialog: false,
        enabled : false,
        filters : systemFilter,
        footerProps: {'items-per-page-options': this.settings.limits},
        headers: JSON.parse(JSON.stringify(defaultHeader[0])) ,
        items: [],
        loading : false,
        message : null,
        myItem : null,
        options: {
          groupBy : [],
          groupDesc : [],
          itemsPerPage : this.settings.limits[0],
          page : 1,
          sortBy : defaultSort[0],
          sortDesc :defaultSort[1]
        },
        others : this.settings.others,
        pageCount: 0,
        search: this.settings.defaultSearch,
        settingsForm : null,
        settingsTable: null,
        showAdvancedFilter : false,
        slots: slots,
        total: 0,
      })
    },
    created () {
      this.enabled = this.slots[0]
    },
    computed: {
      showSelect () {
        return this.isEnabled('select')
      },
      key(){
        var value = "";
        var keys = this.settings.key
        for (var k = 0; k < keys.length ; k++){
          if (k > 0){
            value += "-";
          }
          value += "${"+keys[k] +"}"
        }
        return "eval(`"+value+"`)"
      }
    },
    methods: {
      open(){
        this.dialog = true
      },
      close(){
        this.dialog = false
        this.settingsForm = null
        this.myItem = null
      },
      deleteItem (item) {
        const index = this.items.indexOf(item)
        confirm('Are you sure you want to delete this item?') && this.items.splice(index, 1)
      },
      execOtherActions(actionType,settings){
        Logger("execOtherActions: "+actionType+" "+settings)
        this.settingsForm = null;
        this.myItem = null;
        
        if (this.loading){
          return
        }
        this.loading = true
        this.getDialogObject(actionType,settings).then(result=>{
           this.settingsForm = result.formSettings
           if (this.settingsForm != null){
              this.open()
           }
        })
        
        this.loading = false
      },
      execAction(id,actionUrl,actionType,settings){
        Logger("execAction: "+ id+" "+actionUrl+" "+actionType+" "+settings)
        this.settingsForm = null;
        this.myItem = null;
        if (this.loading){
          return
        }
        this.loading = true
        
        Promise.all([this.getObject(actionUrl,id),this.getDialogObject(actionType,settings)]).then((results)=>{
          this.myItem = results[0].myItem
          this.settingsForm = results[1].formSettings
          if (this.settingsForm != null && this.myItem != null){
              this.open()
            }
          }).catch(error => { 
              this.message = error.message
          });
        
        this.loading = false
      },
      async getDialogObject(actionType,settings){
        return new Promise((resolve) => {
          let formSettings = null
          
          this.$store.dispatch('datatable/loadDialog', {"actionType": actionType, "id":settings.id}).then(
            dialog => {
              if (dialog.data.status){
                formSettings = dialog.data.data.settings
                resolve({
                  formSettings
                })
              } else {
                this.message = dialog.data.userMessageError+"."+dialog.data.technicalMessageError
              }
            },
            error => {
                this.message = error
            }
          );

          setTimeout(() => { 
            this.loading = false
            this.message = null
            resolve({
              formSettings
            })
          }, 10000)

        })        
      },
      async getObject(actionUrl,id){
        let myItem = null
        return new Promise((resolve) => {
            
            this.$store.dispatch('datatable/loadObject', {"actionUrl": actionUrl, "id":id}).then(
              object => {
                if (object.data.status){
                  myItem = object.data.data
                  resolve({
                    myItem
                  })
                } else {
                  console.log("Implement erro backend")
                }
              },
              error => {
                  console.log(error)
              }
            );

          setTimeout(() => { 
            this.loading = false
            resolve({
              myItem
            })
          }, 10000)
          
        })
      },
      getDataFromApi () {
        Logger("getDataFromApi")
        this.loading = true 
        return new Promise((resolve) => {
          const { sortBy, sortDesc, page, itemsPerPage } = this.options

          let items = []
          let total = 0
          const orders = []
          //ReWork
          for(let i = 0; i < sortBy.length; i++){
              const order = {"attribute":sortBy[i],"order":(sortDesc[i]?"asc":"desc")}
              orders.push(order)
          }

          const columnFilters = []
          for (let i = 0; i< this.filters.length;i++){
            const columnFilter = {
              "attribute":this.filters[i].attribute,
              "operation":this.filters[i].operation,
              "value":this.filters[i].value,
              "condition":this.filters[i].condition
            }
            columnFilters.push(columnFilter)
          }
          
          const params = {
            "filters": 
            {
              "orders":orders,
              "searchValue":this.search==null?"":this.search,
              "offset":page-1,
              "limit":itemsPerPage,
              "columns":columnFilters
            }
          }

          this.$store.dispatch('datatable/loadTable', {"path": this.settings.url, "data":params}).then(
              table => {
                if (table.data.status){
                  items = table.data.data
                  total = table.data.rows
                  if (total>0 && items.length==0 && this.options.page >0){
                    this.options.page = 0
                  }
                } else {
                  this.message = table.data.userMessageError+"."+table.data.technicalMessageError
                }
                this.loading = false
                resolve({
                  items,
                  total,
                })
              },
              error => {
                this.message = error
                this.loading = false
              }
          );
        
          setTimeout(() => { 
            this.loading = false
            resolve({
              items,
              total,
            })
          }, 10000)
        })
      },
      getDefaultHeader(){
        let defaultHeaders = []
        let defaultRowActions = []
        for (let i = 0;i<this.settings.fields.length;i++){
          if (this.settings.fields[i].field!= null){
            const item = {
              "text": this.settings.fields[i].field.title,
              "align": i==0?'start':'start',
              "sortable": this.settings.fields[i].sortable,
              "filterable": true,
              "value": this.settings.fields[i].field.attribute,
            }
            defaultHeaders.push(item)
          } else {
            const item = {
              "id": this.settings.fields[i].id, 
              "icon": this.settings.fields[i].icon,
              "actionUrl" : this.settings.fields[i].actionUrl,
              "actionType": this.settings.fields[i].actionType,
              "actionSettings": this.settings.fields[i].actionSettings,
              "actionDescription": this.settings.fields[i].actionDescription,
              "width" : this.settings.fields[i].width
            }
            defaultRowActions.push(item)
          }
        }
        return [defaultHeaders, defaultRowActions ]
      },
      getThisInst(){
        return this
      },
      getDefaultSort(){
        let sortBy = []
        let sortDesc = []
        let defaultSort = this.settings.defaultSort
        for(let i = 0; i < defaultSort.length;i++){
          sortBy.push(defaultSort[i].attribute)
          sortDesc.push(defaultSort[i].order=="asc"?true:false)
        }
        return [sortBy,sortDesc]
      },
      getKey(item){
        let value = "";
        if (item!=null){
          for (var k = 0; k < this.settings.key.length ; k++){
            if (k > 0){
              value += "/";
            }
            value += eval("item."+this.settings.key[k]).toString();
          }
        }
        return value
      },
      isEnabled (slot) {
        return this.enabled === slot
      },
      loadAdvancedFilter(){
        this.getDataFromApi()
            .then(data => {
              this.items = data.items
              this.total = data.total
            })
      }
    },
    watch: {
      dialog (val) {
        val || this.close()
        if (!val){
          this.getDataFromApi()
            .then(data => {
              this.items = data.items
              this.total = data.total
            })
        }
        
      },
      enabled (slot) {
        this.headers = JSON.parse(JSON.stringify(this.defaultHeader[0])) 
        if (slot === 'default'){
          this.headers.push({ text: 'Actions',value: 'actions',sortable: false })
        }
      },
      options: {
        handler () {
          this.getDataFromApi()
            .then(data => {
              this.items = data.items
              this.total = data.total
          })
        },
        deep: true,
      },
      search: {
        handler () {
          this.getDataFromApi()
            .then(data => {
              this.items = data.items
              this.total = data.total
          })
        }
      }
    },
   
}


</script>
