import Logger from '../services/logger.service'

export const attribute = {
    methods: {
        getDatalist(attribute){
            let datalist = null
            for (var i=0; i<this.datalists.length;i++){
                if (this.datalists[i][0]==attribute){
                    datalist = this.datalists[i][1]
                    break
                }
            }
            if (datalist==null){
                this.datalists.push([attribute,[] ])
                datalist = this.datalists[this.datalists.length-1][1]
                //fix bug autocomplete 
                var value = eval(`this.item.${attribute}`) 
                if (value != null && value != ''){
                    datalist.push(value)
                }
                //fix bug autocomplete
            }
            return datalist;
        },
        getRules(attribute, mandatory, type ){
            let rule = null
            for (var i=0; i<this.rules.length;i++){
                if (this.rules[i][0]==attribute){
                    rule = this.rules[i][1]
                    break
                }
            }
            if (rule==null){
                let defaultRule = []
                if (mandatory)
                    defaultRule.push( v => !!v || 'Field is required')

                if (type == 'email')
                    defaultRule.push(v => /.+@.+\..+/.test(v) || 'Field must be valid')
                    
                this.rules.push([attribute,defaultRule])
                rule = this.rules[this.rules.length-1][1]
            }
            return rule
        },
        isInput(attribute){
            Logger("isInput: "+ attribute)
            return attribute.indexOf("[") == -1;
        },
        onInput(key,value){
          Logger("onInput: "+ key)
          if (key.indexOf(".")>0){
            let aux = this.item
            let auxKey = key
            let auxValue = value
            let position = -1
            while((position = auxKey.indexOf("."))>0){
                aux = aux[auxKey.substring(0,position)]
                auxValue = typeof auxValue=='undefined'?auxValue:auxValue[auxKey.substring(0,position)]
                auxKey = auxKey.substring(position+1)
            }
            auxValue =typeof auxValue=='undefined'?auxValue:eval(`auxValue.${auxKey}`)
            this.$set(aux,auxKey, auxValue)
          } else {
            this.$set(this.item,key, value);
          }
        },
        onValue(type,key) {
            let value = eval(`this.item.${key}`) 
            Logger("onValue: attribute - "+ key)
            Logger("onValue: value - "+ value)
            return type=='hidden' && typeof value == 'object'?JSON.stringify(value):value
        }
    },
}
