<template>
    <input v-if="f.field.type == 'HIDDEN' && isInput(f.field.attribute)" type="hidden" :value="onValue(f.field.type,f.field.attribute)" >
    <v-autocomplete v-else-if="f.search != null && isInput(f.field.attribute)" :id="f.field.attribute"
        :disabled="f.readonly"
        :label="f.field.title"
        :messages="f.field.observation"
        :name="f.field.attribute"
        :placeholder="f.field.help"
        :readonly="f.readonly"
        :required="f.mandatory"            
        :rules="getRules(f.field.attribute,f.mandatory,f.field.type)"
        :value="onValue(f.field.type,f.field.attribute)" 
        @input="onInput(f.field.attribute,$event)" 
        :items="getDatalist(f.field.attribute)"  
        @input.native="loadAutoComplete"
        @change.native="onChangeAutoComplete" 
        :item-text="f.field.attribute"
        :item-value="f.field.attribute"
        :no-filter=true  
        return-object> 
        
        <template slot="item" slot-scope="data" >     
            <template v-if="typeof data.item !== 'object'" >   
                <v-list-item-content v-text="data.item"></v-list-item-content>   
            </template>
            <template v-else>        
                    <v-list-item-content> 
                    <v-list-item-title class="v-list-item__title">{{getAutoCompleteHeader(data,f.field.attribute)}}</v-list-item-title>  
                    <v-list-item-subtitle class="v-list-item__subtitle">{{getAutoCompleteBody(data,f.field.attribute)}}</v-list-item-subtitle>    
                </v-list-item-content>
            </template>
        </template>
    </v-autocomplete> 
    <v-checkbox v-else-if="f.field.type == 'CHECKBOX' && isInput(f.field.attribute)" :id="f.field.attribute"
                :disabled="f.readonly"
                :label="f.field.title"
                :messages="f.field.observation"
                :name="f.field.attribute"
                :placeholder="f.field.help"
                :readonly="f.readonly"
                :required="f.mandatory"            
                :rules="getRules(f.field.attribute,f.mandatory,f.field.type)"
                :input-value="onValue(f.field.type,f.field.attribute)" 
                @change="onInput(f.field.attribute,$event)">
    </v-checkbox>
    <v-text-field v-else-if="f.field.type == 'DATE' && isInput(f.field.attribute)" :id="f.field.attribute"
                type="date" :disabled="f.readonly"
                :label="f.field.title"
                :messages="f.field.observation"
                :name="f.field.attribute"
                :placeholder="f.field.help"
                :readonly="f.readonly"
                :required="f.mandatory"            
                :rules="getRules(f.field.attribute,f.mandatory,f.field.type)"
                :value="onValue(f.field.type,f.field.attribute)" 
                @input="onInput(f.field.attribute,$event)"  >
    </v-text-field>
    <v-text-field v-else-if="f.field.type == 'DATETIME' && isInput(f.field.attribute)" :id="f.field.attribute"
                type="datetime-local" :disabled="f.readonly"
                :label="f.field.title"
                :messages="f.field.observation"
                :name="f.field.attribute"
                :placeholder="f.field.help"
                :readonly="f.readonly"
                :required="f.mandatory"            
                :rules="getRules(f.field.attribute,f.mandatory,f.field.type)"
                :value="onValue(f.field.type,f.field.attribute)" 
                @input="onInput(f.field.attribute,$event)"  >
    </v-text-field>
    <v-text-field v-else-if="f.field.type == 'EMAIL' && isInput(f.field.attribute)" :id="f.field.attribute"
                type="email" :disabled="f.readonly"
                :label="f.field.title"
                :messages="f.field.observation"
                :name="f.field.attribute"
                :placeholder="f.field.help"
                :readonly="f.readonly"
                :required="f.mandatory"            
                :rules="getRules(f.field.attribute,f.mandatory,f.field.type)"
                :value="onValue(f.field.type,f.field.attribute)" 
                @input="onInput(f.field.attribute,$event)"  >
    </v-text-field>
    <v-file-input v-else-if="f.field.type == 'FILE' && isInput(f.field.attribute)" :id="f.field.attribute"
                accept="image/*"
                show-size
                :disabled="f.readonly"
                :label="f.field.title"
                :messages="f.field.observation"
                :name="f.field.attribute"
                :placeholder="f.field.help"
                :readonly="f.readonly"
                :required="f.mandatory" 
                :rules="getRules(f.field.attribute,f.mandatory,f.field.type)"
                @change="selectFile(f.field.attribute,$event)"
                append-icon="mdi-paperclip"
                prepend-icon="">
    </v-file-input>
    <v-text-field v-else-if="f.field.type == 'NUMBER' && isInput(f.field.attribute)" :id="f.field.attribute"
                type="number" :disabled="f.readonly"
                :label="f.field.title"
                :messages="f.field.observation"
                :name="f.field.attribute"
                :placeholder="f.field.help"
                :readonly="f.readonly"
                :required="f.mandatory"            
                :rules="getRules(f.field.attribute,f.mandatory,f.field.type)"
                :value="onValue(f.field.type,f.field.attribute)" 
                @input="onInput(f.field.attribute,$event)"  >
    </v-text-field>
    <v-text-field v-else-if="f.field.type == 'PASSWORD' && isInput(f.field.attribute)" :id="f.field.attribute"
                type="password" :disabled="f.readonly"
                :label="f.field.title"
                :messages="f.field.observation"
                :name="f.field.attribute"
                :placeholder="f.field.help"
                :readonly="f.readonly"
                :required="f.mandatory"            
                :rules="getRules(f.field.attribute,f.mandatory,f.field.type)"
                :value="onValue(f.field.type,f.field.attribute)" 
                @input="onInput(f.field.attribute,$event)"  >
    </v-text-field>
    <v-select v-else-if="f.field.type.includes('SELECT') && isInput(f.field.attribute)" :id="f.field.attribute"
                :disabled="f.readonly"
                :label="f.field.title"
                :messages="f.field.observation"
                :name="f.field.attribute"
                :placeholder="f.field.help"
                :readonly="f.readonly"
                :required="f.mandatory"  
                :rules="getRules(f.field.attribute,f.mandatory,f.field.type)"
                :value="onValue(f.field.type,f.field.attribute)" 
                @input="onInput(f.field.attribute,$event)"
                :items="parseJSONAttribute(f.field.value)"
                item-value="value"
                item-text="description"
    ></v-select>
    <v-text-field v-else-if="f.field.type == 'TEXT' && isInput(f.field.attribute)" :id="f.field.attribute"
                type="text" :disabled="f.readonly"
                :label="f.field.title"
                :messages="f.field.observation"
                :name="f.field.attribute"
                :placeholder="f.field.help"
                :readonly="f.readonly"
                :required="f.mandatory"            
                :rules="getRules(f.field.attribute,f.mandatory,f.field.type)"
                :value="onValue(f.field.type,f.field.attribute)" 
                @input="onInput(f.field.attribute,$event)"  >
    </v-text-field>
    <v-textarea v-else-if="f.field.type == 'TEXTAREA' && isInput(f.field.attribute)" :id="f.field.attribute"
                type="textarea" :disabled="f.readonly"
                :label="f.field.title"
                :messages="f.field.observation"
                :name="f.field.attribute"
                :placeholder="f.field.help"
                :readonly="f.readonly"
                :required="f.mandatory"   
                :rows="f.rows"         
                :rules="getRules(f.field.attribute,f.mandatory,f.field.type)"
                :value="onValue(f.field.type,f.field.attribute)" 
                @input="onInput(f.field.attribute,$event)"  >
    </v-textarea>
    <v-text-field v-else-if="f.field.type == 'TIME' && isInput(f.field.attribute)" :id="f.field.attribute"
                type="time" :disabled="f.readonly"
                :label="f.field.title"
                :messages="f.field.observation"
                :name="f.field.attribute"
                :placeholder="f.field.help"
                :readonly="f.readonly"
                :required="f.mandatory"            
                :rules="getRules(f.field.attribute,f.mandatory,f.field.type)"
                :value="onValue(f.field.type,f.field.attribute)" 
                @input="onInput(f.field.attribute,$event)"  >
    </v-text-field>
</template>

<script>
import {formcomputedattributes } from '../mixins/formcomputedattributes'
import {attribute } from '../mixins/attribute'
import {autocomplete } from '../mixins/autocomplete'
import {parsejson } from '../mixins/parsejson'

export default {
    name: 'Field',
    props: {
        f : Object
    },
    mixins : [formcomputedattributes,attribute,parsejson,autocomplete],
    data (){
        return ({

        })
    },
    methods : {
        selectFile(attribute,file){
            this.files.push({"key":attribute,"content":file})
        }
    }
}
</script>
