export const parsejson = {
    methods:{
        parseJSONObject(json){
            for (let loop in json){
                var value = this.parseJSONAttribute(json[loop]);
                json[loop] = value;
                if (typeof json[loop] == "object"){
                    this.parseJSONObject(json[loop]);
                }		
            }	
            return json;
        },
        parseJSONAttribute(value){
            var isFunction = typeof value == "string" && value.indexOf("FUNCTION ") > -1 && value.indexOf("FUNCTION ") < 15 && value.indexOf("(") != -1 && value.indexOf(")") != -1;
            if (isFunction){
                value = value.substring(value.indexOf("FUNCTION ")+9);
                value = eval(value);
            } 
            return value;
        },
    }
}